"use client";

import { useRouter } from 'next/navigation';
import Link from 'next/link';
import axios from 'axios';

interface NavigationProps {
  role?: 'admin' | 'user';
}

export default function Navigation({ role }: NavigationProps) {
  const router = useRouter();

  const handleLogout = async () => {
    try {
      await axios.post('/api/auth/logout');
      router.push('/auth/login');
    } catch (error) {
      console.error('Error al cerrar sesión:', error);
    }
  };

  return (
    <nav className="bg-white shadow-lg">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-between h-16">
          <div className="flex">
            <Link 
              href={role === 'admin' ? '/products' : '/usuarios'}
              className="flex items-center text-gray-800 hover:text-gray-600 px-3 py-2 rounded-md text-sm font-medium"
            >
              BoardGames ERP
            </Link>
          </div>

          <div className="flex items-center">
            {role === 'admin' && (
              <>
                <Link
                  href="/products"
                  className="text-gray-800 hover:text-gray-600 px-3 py-2 rounded-md text-sm font-medium"
                >
                  Gestionar Juegos
                </Link>
                <Link
                  href="/products/new"
                  className="text-gray-800 hover:text-gray-600 px-3 py-2 rounded-md text-sm font-medium"
                >
                  Agregar Juego
                </Link>
              </>
            )}
            {role === 'user' && (
              <Link
                href="/usuarios"
                className="text-gray-800 hover:text-gray-600 px-3 py-2 rounded-md text-sm font-medium"
              >
                Ver Juegos
              </Link>
            )}
            <button
              onClick={handleLogout}
              className="ml-4 bg-red-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-red-700"
            >
              Cerrar Sesión
            </button>
          </div>
        </div>
      </div>
    </nav>
  );
}
